import {
  BigNumber,
  utils as ethersUtils,
  constants as ethersConstants,
} from 'ethers'

const { formatEther } = ethersUtils;
const { Zero } = ethersConstants;

type FormatBalance = (balance?: BigNumber, maxDecimalDigits?: number) => string

export const formatBalance: FormatBalance = (balance = Zero, maxDecimalDigits = 4) => {
  const balanceString = formatEther(balance)

  if (balanceString.includes('.')) {
    const parts = balanceString.split('.')
    return parts[0] + '.' + parts[1].slice(0, maxDecimalDigits)
  }

  return balanceString
}
